.TextInput {
  font-size: large;
  border: 1px solid #eeeeee;
  padding: 5px;
  border-radius: 3px;
  display: block;
  margin: 5px auto;
}
.TextInput:focus {
  outline: none;
  border-color: #719ece;
  box-shadow: 0 0 10px rgb(50, 100, 153);
}
.Radio {
  font-size: small;
}
.Button {
  display: block;
  margin: 10px auto;
  padding: 0.5rem;
  font-size: 1em;
  background: darkcyan;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  margin: 5px auto;
}
